"use client"

import { useState, useEffect, useRef } from 'react'
import QrScanner from 'qr-scanner'

interface InventoryItem {
  id: string
  manufacturer: string
  manufacturing_date: string
  condition: string
  transaction_date: string
  buy_date?: string
  repair_date?: string
  install_date?: string
  description?: string
}

interface QRScannerProps {
  onScanResult: (partId: string, partData: InventoryItem | null) => void
}

export default function QRScanner({ onScanResult }: QRScannerProps) {
  const videoRef = useRef<HTMLVideoElement>(null)
  const qrScannerRef = useRef<QrScanner | null>(null)
  const [isScanning, setIsScanning] = useState(false)
  const [hasCamera, setHasCamera] = useState(false)
  const [hasFlash, setHasFlash] = useState(false)
  const [flashEnabled, setFlashEnabled] = useState(false)
  const [error, setError] = useState<string | null>(null)
  const [permissionStatus, setPermissionStatus] = useState<'pending' | 'granted' | 'denied'>('pending')

  useEffect(() => {
    checkCameraSupport()
    return () => {
      stopScanning()
    }
  }, [])

  const checkCameraSupport = async () => {
    try {
      const hasCamera = await QrScanner.hasCamera()
      setHasCamera(hasCamera)
      
      if (hasCamera) {
        // Check for flash support
        const stream = await navigator.mediaDevices.getUserMedia({ 
          video: { facingMode: 'environment' } 
        })
        const track = stream.getVideoTracks()[0]
        const capabilities = track.getCapabilities()
        setHasFlash('torch' in capabilities)
        
        // Stop the test stream
        stream.getTracks().forEach(track => track.stop())
      }
    } catch (error) {
      console.error('Camera check failed:', error)
      setHasCamera(false)
    }
  }

  const startScanning = async () => {
    if (!videoRef.current || !hasCamera) return

    try {
      setError(null)
      setIsScanning(true)

      qrScannerRef.current = new QrScanner(
        videoRef.current,
        (result) => handleScanResult(result.data),
        {
          highlightScanRegion: true,
          highlightCodeOutline: true,
          preferredCamera: 'environment'
        }
      )

      await qrScannerRef.current.start()
      setPermissionStatus('granted')
    } catch (error) {
      console.error('Failed to start scanner:', error)
      setError('Failed to access camera. Please check permissions.')
      setPermissionStatus('denied')
      setIsScanning(false)
    }
  }

  const stopScanning = () => {
    if (qrScannerRef.current) {
      qrScannerRef.current.stop()
      qrScannerRef.current.destroy()
      qrScannerRef.current = null
    }
    setIsScanning(false)
    setFlashEnabled(false)
  }

  const toggleFlash = async () => {
    if (!qrScannerRef.current || !hasFlash) return

    try {
      const newFlashState = !flashEnabled
      // Use the turnFlashOn/turnFlashOff methods instead
      if (newFlashState) {
        await qrScannerRef.current.turnFlashOn()
      } else {
        await qrScannerRef.current.turnFlashOff()
      }
      setFlashEnabled(newFlashState)
    } catch (error) {
      console.error('Failed to toggle flash:', error)
      // Flash might not be supported, disable the feature
      setHasFlash(false)
    }
  }

  const handleScanResult = (data: string) => {
    try {
      // Try to parse as JSON first (our QR codes)
      const qrData = JSON.parse(data)
      if (qrData.type === 'bus_part' && qrData.id) {
        findPartData(qrData.id)
        return
      }
    } catch {
      // If not JSON, treat as plain text part ID
      findPartData(data)
    }
  }

  const findPartData = (partId: string) => {
    try {
      const inventoryData = localStorage.getItem('inventoryData')
      if (inventoryData) {
        const items: InventoryItem[] = JSON.parse(inventoryData)
        const partData = items.find(item => item.id === partId)
        onScanResult(partId, partData || null)
      } else {
        onScanResult(partId, null)
      }
    } catch (error) {
      console.error('Failed to load inventory data:', error)
      onScanResult(partId, null)
    }
  }

  const requestCameraPermission = async () => {
    try {
      await navigator.mediaDevices.getUserMedia({ video: true })
      setPermissionStatus('granted')
      await checkCameraSupport()
    } catch (error) {
      setPermissionStatus('denied')
      setError('Camera permission denied. Please enable camera access in your browser settings.')
    }
  }

  if (!hasCamera && permissionStatus !== 'pending') {
    return (
      <div className="bg-white rounded-lg shadow-md p-6 text-center">
        <div className="text-red-400 mb-4">
          <div className="w-16 h-16 mx-auto mb-4 bg-red-100 rounded-full flex items-center justify-center">
            <span className="text-2xl">üì∑</span>
          </div>
        </div>
        <h3 className="text-lg font-medium text-gray-900 mb-2">Camera Not Available</h3>
        <p className="text-gray-600">
          This device doesn't have a camera or camera access is not available.
        </p>
      </div>
    )
  }

  if (permissionStatus === 'denied') {
    return (
      <div className="bg-white rounded-lg shadow-md p-6 text-center">
        <div className="text-yellow-400 mb-4">
          <div className="w-16 h-16 mx-auto mb-4 bg-yellow-100 rounded-full flex items-center justify-center">
            <span className="text-2xl">‚ö†Ô∏è</span>
          </div>
        </div>
        <h3 className="text-lg font-medium text-gray-900 mb-2">Camera Permission Required</h3>
        <p className="text-gray-600 mb-4">
          Please allow camera access to scan QR codes.
        </p>
        <button
          onClick={requestCameraPermission}
          className="bg-blue-600 text-white px-4 py-2 rounded-md hover:bg-blue-700 transition-colors"
        >
          Enable Camera
        </button>
      </div>
    )
  }

  return (
    <div className="bg-white rounded-lg shadow-md overflow-hidden">
      <div className="p-4 bg-gray-50 border-b">
        <h3 className="text-lg font-semibold text-gray-900">QR Code Scanner</h3>
        <p className="text-sm text-gray-600">Point your camera at a QR code to scan</p>
      </div>

      <div className="relative">
        <video
          ref={videoRef}
          className="w-full h-64 sm:h-80 object-cover bg-black"
          playsInline
          muted
        />
        
        {!isScanning && (
          <div className="absolute inset-0 bg-black bg-opacity-50 flex items-center justify-center">
            <button
              onClick={startScanning}
              disabled={!hasCamera}
              className="bg-blue-600 text-white px-6 py-3 rounded-lg hover:bg-blue-700 transition-colors disabled:opacity-50 disabled:cursor-not-allowed"
            >
              Start Scanning
            </button>
          </div>
        )}

        {isScanning && hasFlash && (
          <button
            onClick={toggleFlash}
            className={`absolute top-4 right-4 p-2 rounded-full ${
              flashEnabled 
                ? 'bg-yellow-500 text-white' 
                : 'bg-gray-800 bg-opacity-50 text-white'
            } hover:bg-opacity-75 transition-colors`}
          >
            <span className="text-lg">üî¶</span>
          </button>
        )}
      </div>

      <div className="p-4">
        {error && (
          <div className="bg-red-50 border border-red-200 rounded-md p-3 mb-4">
            <p className="text-red-800 text-sm">{error}</p>
          </div>
        )}

        <div className="flex justify-between items-center">
          <div className="text-sm text-gray-600">
            {isScanning ? (
              <span className="flex items-center">
                <div className="w-2 h-2 bg-green-500 rounded-full mr-2 animate-pulse"></div>
                Scanning for QR codes...
              </span>
            ) : (
              'Camera ready'
            )}
          </div>

          {isScanning && (
            <button
              onClick={stopScanning}
              className="bg-red-600 text-white px-4 py-2 rounded-md hover:bg-red-700 transition-colors text-sm"
            >
              Stop Scanning
            </button>
          )}
        </div>
      </div>
    </div>
  )
}
