"use client"

import { useState } from 'react'
import Link from 'next/link'
import QRScanner from '@/components/QRScanner'

interface InventoryItem {
  id: string
  manufacturer: string
  manufacturing_date: string
  condition: string
  transaction_date: string
  buy_date?: string
  repair_date?: string
  install_date?: string
  description?: string
}

export default function ScanPage() {
  const [scannedPartId, setScannedPartId] = useState<string | null>(null)
  const [partData, setPartData] = useState<InventoryItem | null>(null)
  const [showScanner, setShowScanner] = useState(true)

  const handleScanResult = (partId: string, data: InventoryItem | null) => {
    setScannedPartId(partId)
    setPartData(data)
    setShowScanner(false)
  }

  const resetScanner = () => {
    setScannedPartId(null)
    setPartData(null)
    setShowScanner(true)
  }

  const formatDate = (dateString: string) => {
    if (!dateString) return 'N/A'
    try {
      return new Date(dateString).toLocaleDateString()
    } catch {
      return dateString
    }
  }

  const getConditionColor = (condition: string) => {
    switch (condition?.toLowerCase()) {
      case 'brand new':
        return 'text-green-600 bg-green-50 border-green-200'
      case 'used':
        return 'text-yellow-600 bg-yellow-50 border-yellow-200'
      case 'repaired':
        return 'text-blue-600 bg-blue-50 border-blue-200'
      default:
        return 'text-gray-600 bg-gray-50 border-gray-200'
    }
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-purple-50 to-pink-100 p-4">
      <div className="max-w-2xl mx-auto pt-8">
        <div className="flex items-center mb-6">
          <Link href="/" className="text-purple-600 hover:text-purple-800 mr-4">
            ‚Üê Back to Home
          </Link>
          <h1 className="text-2xl font-bold text-gray-900">Scan QR Code</h1>
        </div>

        {showScanner && (
          <div className="mb-6">
            <QRScanner onScanResult={handleScanResult} />
          </div>
        )}

        {scannedPartId && (
          <div className="bg-white rounded-lg shadow-md p-6 mb-6">
            <div className="flex justify-between items-start mb-4">
              <h2 className="text-xl font-semibold text-gray-900">Scan Result</h2>
              <button
                onClick={resetScanner}
                className="bg-purple-600 text-white px-3 py-1 rounded-md hover:bg-purple-700 transition-colors text-sm"
              >
                Scan Again
              </button>
            </div>

            {partData ? (
              <div className="space-y-4">
                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                  <div className="bg-gray-50 p-4 rounded-lg">
                    <h3 className="font-semibold text-gray-900 mb-2">Part Information</h3>
                    <div className="space-y-2 text-sm">
                      <div>
                        <span className="font-medium text-gray-700">ID:</span>
                        <span className="ml-2 text-gray-900 font-mono">{partData.id}</span>
                      </div>
                      <div>
                        <span className="font-medium text-gray-700">Manufacturer:</span>
                        <span className="ml-2 text-gray-900">{partData.manufacturer}</span>
                      </div>
                      <div>
                        <span className="font-medium text-gray-700">Condition:</span>
                        <span className={`ml-2 px-2 py-1 rounded-full text-xs font-medium border ${getConditionColor(partData.condition)}`}>
                          {partData.condition}
                        </span>
                      </div>
                      {partData.description && (
                        <div>
                          <span className="font-medium text-gray-700">Description:</span>
                          <span className="ml-2 text-gray-900">{partData.description}</span>
                        </div>
                      )}
                    </div>
                  </div>

                  <div className="bg-gray-50 p-4 rounded-lg">
                    <h3 className="font-semibold text-gray-900 mb-2">Dates</h3>
                    <div className="space-y-2 text-sm">
                      <div>
                        <span className="font-medium text-gray-700">Manufactured:</span>
                        <span className="ml-2 text-gray-900">{formatDate(partData.manufacturing_date)}</span>
                      </div>
                      <div>
                        <span className="font-medium text-gray-700">Transaction:</span>
                        <span className="ml-2 text-gray-900">{formatDate(partData.transaction_date)}</span>
                      </div>
                      {partData.buy_date && (
                        <div>
                          <span className="font-medium text-gray-700">Purchased:</span>
                          <span className="ml-2 text-gray-900">{formatDate(partData.buy_date)}</span>
                        </div>
                      )}
                      {partData.repair_date && (
                        <div>
                          <span className="font-medium text-gray-700">Repaired:</span>
                          <span className="ml-2 text-gray-900">{formatDate(partData.repair_date)}</span>
                        </div>
                      )}
                      {partData.install_date && (
                        <div>
                          <span className="font-medium text-gray-700">Installed:</span>
                          <span className="ml-2 text-gray-900">{formatDate(partData.install_date)}</span>
                        </div>
                      )}
                    </div>
                  </div>
                </div>

                <div className="bg-green-50 border border-green-200 rounded-md p-3">
                  <div className="flex items-center">
                    <span className="text-green-600 mr-2">‚úì</span>
                    <span className="text-green-800 text-sm font-medium">Part found in inventory</span>
                  </div>
                </div>
              </div>
            ) : (
              <div className="text-center py-8">
                <div className="text-yellow-400 mb-4">
                  <div className="w-16 h-16 mx-auto mb-4 bg-yellow-100 rounded-full flex items-center justify-center">
                    <span className="text-2xl">‚ö†Ô∏è</span>
                  </div>
                </div>
                <h3 className="text-lg font-medium text-gray-900 mb-2">Part Not Found</h3>
                <p className="text-gray-600 mb-4">
                  Scanned ID: <span className="font-mono bg-gray-100 px-2 py-1 rounded">{scannedPartId}</span>
                </p>
                <p className="text-gray-600 text-sm mb-4">
                  This part ID was not found in the current inventory. Make sure you have uploaded the correct CSV file.
                </p>
                <Link 
                  href="/upload" 
                  className="inline-block bg-blue-600 text-white px-4 py-2 rounded-md hover:bg-blue-700 transition-colors text-sm"
                >
                  Upload Inventory
                </Link>
              </div>
            )}
          </div>
        )}

        <div className="bg-white rounded-lg shadow-md p-6">
          <h3 className="text-lg font-semibold text-gray-900 mb-3">How to Scan</h3>
          <div className="space-y-3 text-sm text-gray-600">
            <div className="flex items-start">
              <div className="flex-shrink-0 w-6 h-6 bg-purple-100 rounded-full flex items-center justify-center mr-3 mt-0.5">
                <span className="text-purple-600 text-xs font-bold">1</span>
              </div>
              <p>Allow camera access when prompted</p>
            </div>
            <div className="flex items-start">
              <div className="flex-shrink-0 w-6 h-6 bg-purple-100 rounded-full flex items-center justify-center mr-3 mt-0.5">
                <span className="text-purple-600 text-xs font-bold">2</span>
              </div>
              <p>Point your camera at the QR code on the bus part</p>
            </div>
            <div className="flex items-start">
              <div className="flex-shrink-0 w-6 h-6 bg-purple-100 rounded-full flex items-center justify-center mr-3 mt-0.5">
                <span className="text-purple-600 text-xs font-bold">3</span>
              </div>
              <p>The part information will appear automatically when scanned</p>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}
